<template><section><html><head></head><body><h1>ModelGraphics</h1>
<p>The <code v-pre="">vc-graphics-model</code> component is used to load a 3D model based on glTF. Need to be used as a subcomponent of <code v-pre="">vc-entity</code>.</p>
<h2>Example</h2>
<h3>Load ModelGraphics with <code v-pre="">vc-graphics-model</code></h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-entity :position="position1" :description="description" :model.sync="model1" :label.sync="label1">
          <vc-graphics-label ref="label" text="Hello Vue Cesium" font="20px sans-serif"></vc-graphics-label>
          <vc-graphics-model ref="model" :uri="uri1"></vc-graphics-model>
        </vc-entity>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
