<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> A boolean Property specifying the visibility of the model.</td>
</tr>
<tr>
<td>uri</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code> A string or Resource Property specifying the URI of the glTF asset.</td>
</tr>
<tr>
<td>scale</td>
<td>Number</td>
<td><code v-pre="">1.0</code></td>
<td><code v-pre="">optional</code> A numeric Property specifying a uniform linear scale.</td>
</tr>
<tr>
<td>minimumPixelSize</td>
<td>Number</td>
<td><code v-pre="">0.0</code></td>
<td><code v-pre="">optional</code> A numeric Property specifying the approximate minimum pixel size of the model regardless of zoom.</td>
</tr>
<tr>
<td>maximumScale</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> The maximum scale size of a model. An upper limit for minimumPixelSize.</td>
</tr>
<tr>
<td>incrementallyLoadTextures</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> Determine if textures may continue to stream in after the model is loaded.</td>
</tr>
<tr>
<td>runAnimations</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> A boolean Property specifying if glTF animations specified in the model should be started.</td>
</tr>
<tr>
<td>clampAnimations</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> A boolean Property specifying if glTF animations should hold the last pose for time durations with no keyframes.</td>
</tr>
<tr>
<td>shadows</td>
<td>Number</td>
<td><code v-pre="">1</code></td>
<td><code v-pre="">optional</code> An enum Property specifying whether the model casts or receives shadows from each light source. <strong>DISABLED: 0, ENABLED: 1, CAST_ONLY: 2, RECEIVE_ONLY: 3</strong></td>
</tr>
<tr>
<td>heightReference</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> A Property specifying what the height is relative to. <strong>NONE: 0, CLAMP_TO_GROUND: 1, RELATIVE_TO_GROUND: 2</strong></td>
</tr>
<tr>
<td>silhouetteColor</td>
<td>Object</td>
<td><code v-pre="">'RED'</code></td>
<td><code v-pre="">optional</code> A Property specifying the Color of the silhouette.</td>
</tr>
<tr>
<td>silhouetteSize</td>
<td>Number</td>
<td><code v-pre="">0.0</code></td>
<td><code v-pre="">optional</code> A numeric Property specifying the size of the silhouette in pixels.</td>
</tr>
<tr>
<td>color</td>
<td>Object</td>
<td><code v-pre="">'WHITE'</code></td>
<td><code v-pre="">optional</code> A Property specifying the Color that blends with the model's rendered color.</td>
</tr>
<tr>
<td>colorBlendMode</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> An enum Property specifying how the color blends with the model. <strong>HIGHLIGHT: 0, REPLACE: 1, MIX: 2</strong></td>
</tr>
<tr>
<td>colorBlendAmount</td>
<td>Number</td>
<td><code v-pre="">0.5</code></td>
<td><code v-pre="">optional</code> A numeric Property specifying the color strength when the colorBlendMode is MIX. A value of 0.0 results in the model's rendered color while a value of 1.0 results in a solid color, with any value in-between resulting in a mix of the two.</td>
</tr>
<tr>
<td>imageBasedLightingFactor</td>
<td>Object</td>
<td><code v-pre="">{x: 1.0, y: 1.0}</code></td>
<td><code v-pre="">optional</code> A property specifying the contribution from diffuse and specular image-based lighting.</td>
</tr>
<tr>
<td>lightColor</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> A property specifying the light color to use when shading the model. The default sun light color will be used when undefined.</td>
</tr>
<tr>
<td>distanceDisplayCondition</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> A NearFarScalar Property used to set pixelOffset based on distance from the camera.A Property specifying at what distance from the camera that this model will be displayed. <strong>structure: { near: number, far: number }</strong></td>
</tr>
<tr>
<td>nodeTransformations</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> An object, where keys are names of nodes, and values are TranslationRotationScale Properties describing the transformation to apply to that node. The transformation is applied after the node's existing transformation as specified in the glTF, and does not replace the node's existing transformation. <strong>structure:{translation: {x: number, y: number, z: number}, rotation: {x: number, y: number, z: number, w: number}, scale:{x: number, y: number, z: number}}</strong></td>
</tr>
<tr>
<td>articulations</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> An object, where keys are composed of an articulation name, a single space, and a stage name, and the values are numeric properties.</td>
</tr>
<tr>
<td>clippingPlanes</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> A property specifying the ClippingPlaneCollection used to selectively disable rendering the model.</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>Refer to the official document: <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/ModelGraphics.html">ModelGraphics</a></strong></li>
</ul>
<h2>Events</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and the cesiumObject.</td>
</tr>
<tr>
<td>definitionChanged</td>
<td></td>
<td>Triggers whenever a property or sub-property is changed or modified.</td>
</tr>
</tbody>
</table>
<hr>
</body></html></section></template>

<script>
    export default {
      data() {
        return {
          description: 'Hello Vue Cesium',
          model1: {},
          label1: {},
          position1: { lng: 114.0, lat: 40.0, height: 1.0 },
          uri1: './statics/SampleData/models/GroundVehicle/GroundVehicle.glb'
        }
      },
      mounted() {
        Promise.all([this.$refs.label.createPromise, this.$refs.model.createPromise]).then(
          (instances) => {
            instances[0].viewer.zoomTo(instances[0].viewer.entities)
          }
        )
      },
      methods: {
        ready(cesiumInstance) {
          const { Cesium, viewer } = cesiumInstance
        }
      }
    }
  </script>